<template>
    <section id="section-game" :class="concertbg">
        <!--  -->
        <transition name="bounce">
            <div class="score_outer" v-if="scoreboard">
                <transition-group class="heart_outer" name="bounce" tag="div">
                    <div v-for="item in heart" :key="item">
                        <img src="../assets/img/heart.png" alt="" class="heart">
                    </div>
                </transition-group>
            </div>
        </transition>
        <!--  -->
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_boy.png" id="character" v-show="lan_file.talk0[i].charactershow" v-if="datesex == 'boy' && character ">
        </transition>
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_girl.png" id="character" v-show="lan_file.talk0[i].charactershow" v-if="datesex == 'girl' && character ">
        </transition>
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_boy.png" id="character" v-show="lan_file.talk2[m].charactershow" v-if="datesex == 'boy' && character ">
        </transition>
        <transition name="fade" :duration="1000">
            <img src="../assets/img/talk_girl.png" id="character" v-show="lan_file.talk2[m].charactershow" v-if="datesex == 'girl' && character ">
        </transition>
        <!--  start -->
        <transition name="fade" :duration="1000">
            <div class="talk-block" v-if="talkblock">
                <div class="name-block"><span>{{lan_file.talk0[i].name}}</span></div>
                <p>{{lan_file.talk0[i].content}}</p>
                <transition  name="gonext" mode="out-in">
                    <button type="button" id="next" @click="gonext" class="btn btn-talknext" v-show="talkbtn"><img src="../assets/img/btn_next.png" alt="" width="30"></button>
                </transition>
            </div>
        </transition>

        <transition name="fade" :duration="1000">
            <div class="talk-block" v-if="talk2block">
                <div class="name-block">{{lan_file.talk2[m].name}}</div>
                <p>{{lan_file.talk2[m].content}}</p>
                <transition  name="gonext" mode="out-in">
                    <button type="button" id="next" @click="gonext2" class="btn btn-talknext" v-show="talkbtn"><img src="../assets/img/btn_next.png" alt="" width="30"></button>
                </transition>
            </div>
        </transition>


        <transition name="fade" :duration="1000">
            <div v-if="nggame" class="nggame_block">
                <div v-for="(game,index) in gamePlay" :key="game.id">
                    <div v-show="u == index">
                        <img :src="game.imgurl" alt="" class="ngimg">
                        <h4 class="question">{{game.question}}</h4>
                        <div class="answer">
                            <button type="button" class="gameoption" v-for="item in game.answerarray" :key="item.id" @click="heartchange(game.id,item.score)">
                                {{item.answer}}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- murmur -->
        <transition name="fade" :duration="1000">
            <div class="talk-block" v-if="murmurblock">
                <p>{{lan_file.talk1[o]}}</p>
                <transition name="gonext" mode="out-in">
                    <button id="next" @click="nextgame" class="btn btn-talknext" v-show="talkbtn"><img src="../assets/img/btn_next.png" alt="" width="30"></button>
                </transition>
            </div>
        </transition>
        <!--  end -->
        <transition name="fade" :duration="1000">
            <div class="clap_outer" v-if="clap" @click="claphand()">
                <button class="btn_clap" ></button>
            </div>
        </transition>





    </section>
</template>
