    },
    methods:{
        gonext(){
            var vobj=this;
            if(this.i < this.lan_file.talk0.length-1){ //i=0,i=1
                this.i++;
                this.talkbtn = false;
                setTimeout(function(){vobj.talkbtn =true; }, 1000);
                console.log('click'+ this.i + 'length:'+ this.lan_file.talk0.length);
            }
            if(this.i == this.lan_file.talk0.length-1){
                this.heartboardappear();
                this.arrangegame();
                console.log(this.gamePlay);
                this.talkblock = false;
                setTimeout(function(){vobj.nggame = true; console.log(vobj.nggame)}, 2000);
                this.$parent.$emit('updategameid', this.gamePlayID);
                this.$parent.$emit('updatemusicid', this.musicid);
            }
        },
        nextgame(){
            var vobj=this;
            this.murmurblock = false;
            setTimeout(function(){vobj.clickgamelimit = true; }, 2000);
            setTimeout(function(){vobj.clickmurmurlimit = true; }, 2000);
            if(this.clickmurmurlimit){
                if(this.o<this.lan_file.talk1.length-1){
                    setTimeout(function(){vobj.nggame = true; }, 500);

                }else{
                    setTimeout(function(){vobj.clap = true; }, 3000);
                    this.$parent.$emit('updategamemusicpause');
                }
            }
        },
        gonext2(){
            var vobj=this;
                if(this.m < this.lan_file.talk2.length){
                    this.m++;
                    this.talkbtn = false;
                    setTimeout(function(){vobj.talkbtn =true; }, 1000);
                    console.log('click'+ this.i + 'length:'+ this.lan_file.talk2.length);
                }
                if(this.m == this.lan_file.talk2.length){
                    console.log();
                    this.talk2block = false;
                    if(this.heart>9){
                        this.$parent.$emit('updatemusicid', 8);
                    }else if(this.heart>6 && this.heart<=9){
                        this.$parent.$emit('updatemusicid', 9);
                    }else{
                        this.$parent.$emit('updatemusicid', 10);
                    }
                    // this.$parent.$emit('updatemusicid', this.musicid);
                    this.$parent.$emit('updatesection', this.sectionid);
                    // setTimeout(function(){vobj.nggame = true; console.log(vobj.nggame)}, 2000);
                }
            
        },
        heartchange(id,scr){
            var vobj=this;
            if(this.clickgamelimit){
                this.clickgamelimit=false;
                this.heart +=scr;
                if(this.heart > 12 ){
                    this.heart = 12;
                }
                if(this.heart < 0){
                    this.heart = 0;
                }
                this.o++;
                this.u++;
                this.nggame = false
            }
            
            
            setTimeout(function(){vobj.murmurblock = true; }, 500);
            if(id==5&&scr!==1){
                this.lan_file.talk1.splice(3,1,this.pushMurmur[0]);
            }
        },
        heartrun(){
            setInterval(this.heartappear, 150);
        },
        heartboardappear(){
            this.scoreboard = true;
            this.heartrun();
        },
        heartappear(){
            if(this.heart < 6 && this.firstheart){
                this.heart++;
            }else{
                clearInterval(this.heartrun);
                this.firstheart = false;
                
            }
        },
        claphand(){
            var vobj = this;
            this.heart++;//++
            this.$parent.$emit('updateheart', this.heart);
            this.clap = false;
            this.clap = false;
            setTimeout(function(){vobj.talk2block = true;vobj.character = true }, 7000);
            console.log(this.gamePlayID);
            this.$parent.$emit('updatemusicid', 7);

        },
        arrangegame(){


         let kill = [0,0,0,0];


         kill[0] = this.gamePlay.findIndex(x => x.id === 5); 

         if(kill[0]>0){
            this.gamePlay.splice(kill[0],1);
            this.gamePlay.unshift(this.gamelang[5]);
        }

        kill[1] = this.gamePlay.findIndex(x => x.id === 7);
        if(kill[1]>0){
            this.gamePlay.splice(kill[1],1);
            this.gamePlay.push(this.gamelang[7]);
        }

        kill[2] = this.gamePlay.findIndex(x => x.id === 8);
        if(kill[2]>0){
            this.gamePlay.splice(kill[2],1);
            this.gamePlay.push(this.gamelang[8]);
        }

        kill[3] = this.gamePlay.findIndex(x => x.id === 6);
        if(kill[3]>0){
            this.gamePlay.splice(kill[3],1);
            this.gamePlay.push(this.gamelang[6]);
        }    

/*

//check
var check5 = true;
var check6 = true;
var check7 = true;
var check8 = true;
for(let i = 0; i < this.gamePlay.length; i++) {
    if (this.gamePlay[i].id === 5 && check5) {
        this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===5),1);
        check5 = false;
    }
    if (this.gamePlay[i].id === 7 && check7) {
        this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===7),1);
        check7 = false;
    }
    if (this.gamePlay[i].id === 8 && check8) {
        this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===8),1);
        check8 = false;
    }
    if (this.gamePlay[i].id === 6 && check6) {
        this.gamePlay.splice(this.gamePlay.findIndex(e => e.id===6),1);
        check6 = false;
    }
}
            //(ifor)
            if(check7 == false){
                this.gamePlay.push(this.gamelang[7]);
            }
            if(check8 == false){
                this.gamePlay.push(this.gamelang[8]);
            }
            if(check6 == false){
                this.gamePlay.push(this.gamelang[6]);
            }
            if(check5 == false){
                this.gamePlay.unshift(this.gamelang[5]);
            }
                this.$parent.$emit('updategameid', this.gamePlayID);

     */  
        console.log(`final:${this.gamePlay.length}`)
        for(let i = 0; i < this.gamePlay.length; i++) {

            console.log(this.gamePlay[i].id);
        }     

        }
    },
    mounted(){
        // var vobj = this;
        if(this.lan == 'ch'){
            this.gamelang = this.gameAll.game_ch;
        }else{
            this.gamelang = this.gameAll.game_en;
        }
        //
        switch(this.concert){
            case 1:
            this.gameUsed = this.gamelang.slice(0,7);
            this.musicid = 3;
                break;
                case 2:
                this.gameUsed = this.gamelang.slice(0,9);
                this.musicid = 4;
                break;
                case 3:
                this.gameUsed = this.gamelang.slice(0,9);
                this.musicid = 5;
                break;
                case 4:
                this.gameUsed = this.gamelang.slice(0,7);
                this.musicid = 6;
                break;
            }
            console.log(`pool:${this.gameUsed.length}`);
            for(var i = 0 ; i < this.gameUsed.length;i++){
               console.log( this.gameUsed[i].id);
           }


           console.log('picking:');
           for(let index=0;index<5;index++){
            let n=Math.floor(Math.random()*this.gameUsed.length);
          //  console.log(n);
          this.gamePlay.push(this.gameUsed[n]);
          console.log(this.gameUsed[n].id);
            this.gameUsed.splice(n,1);//
        }


        console.log(`gamePlay:${this.gamePlay.length}`);
        for(var j = 0 ; j < this.gamePlay.length; j++){
           console.log( this.gamePlay[j].id);
       }


        this.lan_file.talk1.push(this.pushMurmur[1]);//murmur

        // [4,1,3]
    },
    computed:{
        lan_file:function(){    
            if(this.lan =="ch"){
                return this.lan_ch;
            }else{
                return this.lan_en;
            }
        },
        concertbg:function(){
            return `emptyconcert${this.concert}`;
        },
        pushMurmur:function(){    
            if(this.lan =="ch"){
                return this.pushMurmurData.mur_ch;
            }else{
                return this.pushMurmurData.mur_en;
            }
        },
        gamePlayID:function(){
            let IDs =[];
            let vobj = this;
            for(let i =0;i<this.gamePlay.length;i++){
                IDs.push(vobj.gamePlay[i].id)
            }
            return IDs;
        }

    }
}
</script>
<style>
#section-game{
    animation: opacityin 4s;
}
#section-game.emptyconcert1{
    background-image: url(../assets/img/play01.jpg);
}
#section-game.emptyconcert2{
    background-image: url(../assets/img/full2.jpg);
}
#section-game.emptyconcert3{
    background-image: url(../assets/img/full3.jpg);
}
#section-game.emptyconcert4{
    background-image: url(../assets/img/play04.jpg);
}
img#character{
    position: absolute;
    z-index: 1;
}
#section-game .score_outer{
    position: absolute;
    top: 20px;
    right: 20px;
}
#section-game .heart_outer{
    width: 350px;
    height: 39px;
    padding: 10px 19px 7px 19px;
    background-image: url(../assets/img/heart_outer.png);
    display: flex;
    flex-direction: row-reverse;
}
#section-game .heart_outer .heart{
    margin-left: 5px;
}
#section-game .ngimg{
    position: absolute;
    width: 330px;
    top: 70px;
    left: 50%;
    transform: translateX(-50%);
}
#section-game .nggame_block .question {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 12px 30px;
    width: 900px;
    height: 97px;
    left: 50%;
    top: 330px;
    text-align: center;
    font-size: 1.5rem;
    background-color: #252525c4;
    box-shadow: 0 2px 8px -2px #391414;
    transform: translateX(-50%);
    color: #FFF;
}
#section-game .nggame_block .answer {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 900px;
    left: 50%;
    top: 432px;
    transform: translateX(-50%);
    opacity: 0;
    animation: answerin .3s forwards;
    animation-delay: 2s;
}
#section-game .nggame_block .gameoption {
    width: 32%;
    display: inline-block;
    font-size: 22px;
    padding: 15px 17px;
    border: none;
    box-shadow: inset 0 0 6px 0 #583526;
    border-radius: 20px;
    background-color: rgb(153, 109, 75);
    color: #ffffff;
    font-weight: bold;
}
#section-game .clap_outer .btn_clap{
    position: absolute;
    background-image: url(../assets/img/clap.png);
    background-size: contain;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 140px;
    height: 140px;
    background-color: transparent;
    border: none;
}
.bounce-enter-active {
    animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
    0% {
        transform: scale(0);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes opacityin{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes answerin{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
</style>